import { Col, Container, Row } from 'react-bootstrap';
import ImageGallery from 'react-image-gallery';

import './wedding-body.css';
import 'react-image-gallery/styles/css/image-gallery.css';

const images = [
  {
    original: '/images/hotel/wedding/w-6.jpg',
    thumbnail: '/images/hotel/wedding/w-6.jpg',
  },
  {
    original: '/images/hotel/wedding/w-4.jpg',
    thumbnail: '/images/hotel/wedding/w-4.jpg',
  },
  {
    original: '/images/hotel/wedding/w-5.jpg',
    thumbnail: '/images/hotel/wedding/w-5.jpg',
  },
  {
    original: '/images/hotel/wedding/w-1.jpg',
    thumbnail: '/images/hotel/wedding/w-1.jpg',
  },
  {
    original: '/images/hotel/wedding/w-2.jpg',
    thumbnail: '/images/hotel/wedding/w-2.jpg',
  },
  {
    original: '/images/hotel/wedding/w-3.jpg',
    thumbnail: '/images/hotel/wedding/w-3.jpg',
  },
];

function WeddingBody() {
  return (
    <div className="accommodations-body-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h1>HOTEL RIU CANCUN</h1>
            <h4>Celebra tu Boda en Hotel Riu Cancún</h4>
            <p>
              Disfruta del extraordinario placer de casarte en el paraíso; Hotel
              Riu Cancún es el lugar ideal para tu boda y luna de miel.
            </p>
          </Col>
        </Row>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <ImageGallery items={images} />
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default WeddingBody;
